export default {
	data() {
		return {
			//宣传语
			content: '',
			titleMaxLength: 140
		}
	},
	methods: {
		//提交宣传语
		slogned() {
		    if (this.content.length < 1) {
		       uni.showModal({ content: '内容不能为空', showCancel: false, });
		       return;
		    } 
			uni.showLoading({title:'上传中'});
			
		}
	},
	watch: {
	    title() {
	        if (this.title.length > this.titleMaxLength) {
	            this.title = String(this.title).slice(0, this.titleMaxLength);
	        }
	    }
	}
}
